"use client";
import { Router } from 'next/router';
import React, { useState } from 'react';

const Login = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [status, setStatus] = useState("");
  const [loading, setLoading] = useState(false);

  const handleLogin = async (e) => {
    e.preventDefault();
    setLoading(true);
    setStatus("");


    try {
      const res = await fetch("/api/login", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ email, password }),
      });

      const data = await res.json();

      if (res.ok) {
        setStatus(data.message);
       
      } else {
        setStatus(data.error);
      }
    } catch (error) {
      setStatus("Server error. Try again.");
    } finally {
      setLoading(false);
    }
  };

  return (
    <section className="px-6 py-10 flex justify-center items-center min-h-screen bg-white">
      <div className="bg-green-50 shadow-lg rounded-lg p-8 w-full max-w-md min-h-[500px]">
        <h1 className="text-3xl font-bold mb-6 text-center">Login</h1>
        <form onSubmit={handleLogin} className="space-y-6">
          <input
            type="email"
            placeholder="Email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
            className="w-full border border-gray-300 p-3 rounded focus:outline-none focus:ring-2 focus:ring-green-500"
          />
          <input
            type="password"
            placeholder="Password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
            className="w-full border border-gray-300 p-3 rounded focus:outline-none focus:ring-2 focus:ring-green-500"
          />
          <button
            type="submit"
            disabled={loading}
            className="bg-green-600 text-white px-6 py-3 w-full rounded hover:bg-green-800 transition disabled:opacity-50"
          >
            {loading ? "Logging in..." : "Login"}
          </button>
          <div>
            If you Don't have Account?{""} <button 
            type="button"
            onClick={()=>Router.push("/signup")}
            className='text-lg text-gray-900 font-bold'>Signup</button>
          </div>
        </form>
        {status && <p className="mt-4 text-center text-red-600">{status}</p>}
      </div>
    </section>
  );
};

export default Login;